/* notice */
.notice .notice-line {
  position: relative;
}
.notice .notice-line .bg-left {
  width: 50%; height: 100%; 
  position: absolute; 
  top: 0; left: 0; 
  background-color: #333;
}
.notice .notice-line .bg-right {
  position: absolute; 
  top: 0; right: 0; width: 50%; height: 100%; 
  background-color: #f6f5ef;
}
.notice .notice-line .inner {
  overflow: hidden;
  height: 62px; 
  display: flex; 
}
.notice .notice-line .inner .inner__left {
  width: 60%; height: 100%; 
  display: flex; align-items: center; 
  background-color: #333; 
}
.notice .notice-line .inner .inner__right {
  width: 40%; height: 100%; 
  display: flex; justify-content: flex-end; align-items: center;
}
.notice .notice-line .inner .inner__left h2 {
  margin-right: 70px;
  color: #fff; font-size: 17px; font-weight: 700; 
}
.notice .notice-line .inner .inner__left .swiper {
  height: 62px; flex-grow: 1;
}
.notice .notice-line .inner .inner__left .notice-line__more {
  width: 62px; height: 62px; 
  display: flex; justify-content: center; align-items: center;
}
.notice .notice-line .inner .inner__left .notice-line__more .material-icons {
  color: #fff; font-size: 30px;
}
.notice .notice-line .inner .inner__right h2 {
  margin-right: 20px;
  font-size: 17px; font-weight: 700; 
}
.notice .notice-line .inner .inner__right .toggle-promotion {
  width: 62px; height: 62px; 
  display: flex; justify-content: center; align-items: center; 
  cursor: pointer; 
}
.notice .notice-line .inner .inner__right .toggle-promotion .material-icons {
  font-size: 30px;
}
.notice .notice-line .inner .inner__left .swiper .swiper-slide {
  display: flex; align-items: center; 
  height: 62px; 
  color: #fff;
}
.notice .promotion {
  height: 693px; 
  position: relative; 
  background-color: #f6f5ef; 
  transition: height .4s; overflow: hidden;
}
.notice .promotion.hide {
  height: 0;
}
.notice .promotion .swiper {
  width: calc(819px * 3 + 20px); height: 553px; 
  margin-left: calc((819px * 3 + 20px) / -2);
  position: absolute; 
  top: 40px; left: 50%; 
}
.notice .promotion .swiper .swiper-slide {
  opacity: .5; transition: opacity 1s;
}
.notice .promotion .swiper .swiper-slide-active {
  opacity: 1;
}
.notice .promotion .swiper .swiper-slide .btn {
  margin: auto;
  position: absolute; 
  bottom: 0; left: 0; right: 0; 
}
.notice .promotion .swiper .swiper-slide .swiper-pagination {
  margin: auto;
  bottom: 40px; left: 0; right: 0; 
}
.notice .promotion .swiper-pagination .swiper-pagination-bullet {
  width: 12px; height: 12px;
  margin-right: 6px;
  background-image: url(../images/promotion_slide_pager.png);
  background-color: transparent;
  outline: 0;
}
.notice .promotion .swiper-pagination .swiper-pagination-bullet:last-child {
  margin-right: 0;
}
.notice .promotion .swiper-pagination .swiper-pagination-bullet-active {
  background-image: url(../images/promotion_slide_pager_on.png);
}
.notice .promotion .swiper-button-prev,
.notice .promotion .swiper-button-next {
  display: flex; justify-content: center; align-items: center;
  width: 42px; height: 42px;
  position: absolute; 
  top: 300px; z-index: 1;
  border: 2px solid #333; border-radius: 50%;
  background-color: rgba(255, 255, 255, .3);
  outline: 0; cursor: pointer; transition: .4s; user-select: none;
}
.notice .promotion .swiper-button-prev {
  margin-left: -480px; left: 50%;
  color: #669900;
}
.notice .promotion .swiper-button-next {
  margin-right: -480px; right: 50%;
  color: #669900;
}
.notice .promotion .swiper-button-prev::after,
.notice .promotion .swiper-button-next::after {
  /* 가상요소에 아이콘 집어넣는 방법 
  content: '\e872';
  font-family: 'Material Icons';*/
  content: '';
}
.notice .promotion .swiper-button-prev:hover,
.notice .promotion .swiper-button-next:hover {
  background-color: #333; color: #fff;
}

