/* youtube */
.youtube { 
  overflow: hidden; 
  height: 700px; 
  position: relative; 
  background-color: #333; 
}
.youtube .youtube__area { 
  width: 1920px; 
  margin-left: calc(1920px / -2); margin-top: calc(1920px * 9 / 16 / -2); 
  position: absolute; 
  left: 50%; top: 50%;
  background-color: orange; 
}
.youtube .youtube__area::before { 
  content: ""; 
  display: block; 
  width: 100%; height: 0; 
  padding-top: 56.25%;
}
.youtube .youtube__cover { 
  width: 100%; height: 100%; 
  position: absolute; 
  top: 0; left: 0; 
  background-image: url(../images/video_cover_pattern.png); 
  background-color: rgba(0,0,0,.3); 
}
#player { 
  width: 100%; height: 100%;
  position: absolute; 
  top: 0; left: 0; 
}
.youtube .inner { height: 700px; }
.youtube .inner .floating1 { position: absolute; top: 50px; left: 0; }
.youtube .inner .floating2 { position: absolute; top: 350px; left: 150px; }

