/* reserve store */
.reserve-store {
  background: url(../images/reserve_store_bg.jpg) no-repeat center/cover;
}
.reserve-store .inner {
  height: 600px;
  display: flex;
  justify-content: center; align-items: center;
}
.reserve-store .medal {
  width: 334px; height: 334px;
  perspective: 600px; /* 원근감을 주기 위해 사용(부모기준) */
}
.reserve-store .medal .front,
.reserve-store .medal .back {
  width: 334px; height: 334px; 
  backface-visibility: hidden; /* 이미지가 겹쳐있을때 뒤에 있는 이미지를 안보이게 가림 */
  position: absolute;
  transition: 1s; 
}
.reserve-store .medal .front {
  transform: rotateY(0deg);
}
.reserve-store .medal:hover .front {
  transform: rotateY(-180deg);
}
.reserve-store .medal .back {
  transform: rotateY(-180deg);
}
.reserve-store .medal:hover .back {
  transform: rotateY(0deg);
}
.reserve-store .medal .back .btn {
  margin: 0 auto;
  position: absolute;
  top: 240px; left: 0; right: 0;
}

