/* awards */
.awards {
  background-color: #272727;
}
.awards .inner {
  padding: 40px 0;
}
.awards .swiper {
  width: 100%; height: 40px;
}
.awards  .swiper-button-prev,
.awards  .swiper-button-next {
  width: 42px; height: 42px;
  display: flex; justify-content: center; align-items: center;
  position: absolute;
  z-index: 1;
  background-color: rgba(255, 255, 255, .3);
  border: 2px solid rgba(255,255,255,.5);
  border-radius: 50%;
  cursor: pointer; outline: none; transition: .4s;
}
.awards  .swiper-button-prev {
  left: 50%; margin-left: -480px; color: #fff;
}
.awards  .swiper-button-next {
  right: 50%; margin-right: -480px; color: #fff;
}
.awards  .swiper-button-prev:hover,
.awards  .swiper-button-next:hover  {
  background-color: #333; color: #fff;
}
.awards .swiper-button-prev::after,
.awards .swiper-button-next::after {
  content: '';
}

/* top scroll button */
#to-top {
  display: flex; justify-content: center; align-items: center;
  width: 42px; height: 42px;
  position: fixed;bottom: 30px; right: 30px; z-index: 1;
  border: 2px solid #fff; border-radius:  10px;
  background-color: #333; color: #fff;
  cursor: pointer; opacity: 0;
}